import React, { Component } from "react";
import Country from "./components/Country";
import "./App.css";
import { Grid } from "@mui/material";
import NewCountry from "./components/NewCountry";

class App extends Component {
  state = {
    countries: [
      {
        id: 1,
        name: "United States",
        medals: [
          {
            id: 0,
            type: "gold",
            count: 0,
          },
          {
            id: 1,
            type: "silver",
            count: 0,
          },
          {
            id: 2,
            type: "bronze",
            count: 0,
          },
        ],
      },
      {
        id: 2,
        name: "China",
        medals: [
          {
            id: 0,
            type: "gold",
            count: 0,
          },
          {
            id: 1,
            type: "silver",
            count: 0,
          },
          {
            id: 2,
            type: "bronze",
            count: 0,
          },
        ],
      },
      {
        id: 3,
        name: "Germany",
        medals: [
          {
            id: 0,
            type: "gold",
            count: 0,
          },
          {
            id: 1,
            type: "silver",
            count: 0,
          },
          {
            id: 2,
            type: "bronze",
            count: 0,
          },
        ],
      },
    ],
  };

  IncrementMedal = (countryId, type) => {
    if (type === "gold") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      arr[index].medals[0].count++;

      this.setState({ countries: arr });
    } else if (type === "silver") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      arr[index].medals[1].count++;

      this.setState({ countries: arr });
    } else if (type === "bronze") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      arr[index].medals[2].count++;

      this.setState({ countries: arr });
    } else {
      return false;
    }
  };

  DecrementMedal = (countryId, type) => {
    if (type === "gold") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      if (arr[index].medals[0].count < 1) {
        return false;
      } else {
        arr[index].medals[0].count--;
        this.setState({ countries: arr });
      }
    } else if (type === "silver") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      if (arr[index].medals[1].count < 1) {
        return false;
      } else {
        arr[index].medals[1].count--;
        this.setState({ countries: arr });
      }
    } else if (type === "bronze") {
      let arr = this.state.countries;
      let index = arr.findIndex((i) => i.id === countryId);
      if (arr[index].medals[2].count < 1) {
        return false;
      } else {
        arr[index].medals[2].count--;
        this.setState({ countries: arr });
      }
    } else {
      return false;
    }
  };

  AddCountry = (country) => {
    if(country.length > 0){
      let tempArr = this.state.countries;
      let lastIndex = tempArr.length+1;
      tempArr.push({
        id: lastIndex,
        name: country,
        medals: [
          {
            id: 0,
            type: "gold",
            count: 0,
          },
          {
            id: 1,
            type: "silver",
            count: 0,
          },
          {
            id: 2,
            type: "bronze",
            count: 0,
          },
        ]
      });

      this.setState({countries: tempArr})
    }
    else{
      return false
    }
      
  }

  DeleteCountry = (countryRef) => {
      this.state.countries.forEach((country)=>{
        if(country.id === countryRef.id){
          let tempArr = this.state.countries;
          let index = tempArr.indexOf(countryRef);
          tempArr.splice(index, 1);
          this.setState({countries:tempArr});
        }
        else{
          return false;
        }
      })
  }

  render() {
    const { countries } = this.state;
    return (
      <div className="App">
        <Grid container>
          {countries.map((country) => (
            <Grid item xs={4}>
              <Country
                key={country.id}
                country={country}
                onIncrement={this.IncrementMedal}
                onDecrement={this.DecrementMedal}
                onDelete={this.DeleteCountry}
              />
            </Grid>
          ))}          
        </Grid>
        <NewCountry addCountry={this.AddCountry}></NewCountry>
      </div>
    );
  }
}

export default App;
