import { Button, Modal, Typography, TextField } from "@mui/material";
import React, { Component } from "react";
import { Container } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { Fab } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { Box } from "@mui/system";


class NewCountry extends Component {
  // helper method

  state = {
    open: false,
    country: '',
    btnDisabled: true,
  };

  renderPrompt = () => {
    this.setState({ open: true });
  };

  deRenderPrompt = () => {
    this.setState({ open: false });
  };

  setCountry = (event) => {
      this.setState({country: event.target.value})
  }

  setCountryAndButton = (event) => {
    this.setCountry(event);
    this.setButtonState(event);
  }

  setButtonState = (event) => {
      if(event.target.value === ""){
        this.setState({btnDisabled: true})
      }
      else{
        this.setState({btnDisabled: false})
      }
  }
  

  render() {
    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 400,
      bgcolor: 'white',
      border: '2px solid #000',
      boxShadow: 24,
      p: 4,
    };
    const { open, country, btnDisabled } = this.state;
    const {addCountry} = this.props;
    return (
      <Container>
        <Fab onClick={this.renderPrompt} style={{position: "absolute", bottom: "5%", right: "5%"}} color="primary" aria-label="add">
          <AddIcon />
        </Fab>

        {open && (
          <Modal
          open={open}
          >
            
            <Box sx={style}>
            <Button style={{position: "absolute", right: "0", top: "5%"}} onClick={this.deRenderPrompt}>
              <CloseIcon></CloseIcon>
            </Button>
            <Typography id="modal-modal-title" variant="h6" component="h2">
            Create New Country
          </Typography>
          <TextField id="outlined-basic" value={country} onChange={this.setCountryAndButton} label="Country Name" variant="outlined" />
          <Button disabled={btnDisabled} onClick={() => addCountry(country)} style={{position: "absolute", right: "5%", bottom: "10%"}}>
              Add
            </Button>
            </Box>
            
          </Modal>
        )}
      </Container>
    );
  }
}

export default NewCountry;
