import React, { Component } from "react";
import { Button } from "@mui/material";
import { Card } from "@mui/material";
import { Container } from "@mui/material";
// import { Icon } from "@mui/material";
// import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from '@mui/icons-material/Remove';

import Medal from "./Medal";

class Country extends Component {

// helper method
onIncrement(countryId, type){
  console.log(countryId);
  console.log(type)
}

  render() {
    const { onIncrement, onDecrement, country, onDelete } = this.props;

    return (
      <Container>
        <Card className="Country">
          {country.name}

          {country.medals.map((medal) => (
            <Medal 
            key={medal.id} 
            type={medal.type} 
            count={medal.count} 
            onIncrement={() => onIncrement(country.id, medal.type)}
            onDecrement={() => onDecrement(country.id, medal.type)}          
            />
          ))}
          <Button onClick={()=>onDelete(country)} style={{color: "red"}}><RemoveIcon style={{color: "red"}}></RemoveIcon>Delete</Button>
        </Card>
      </Container>
    );
  }
}

export default Country;
